import { CommitPolicy } from '../vpg/vpg-failover/__json__/vpg-failover-commit-policy-type';
import { ShutdownPolicy } from '../vpg/vpg-failover/__json__/vpg-failover-shutdown-policy-type';
import { FailoverParamsRequestJson } from './__json__/failover-params-request-json';

/* istanbul ignore next: autogenerated */
export class FailoverParamsRequest {
  private readonly _json: FailoverParamsRequestJson;

  constructor(failoverParamsRequest: FailoverParamsRequest);
  constructor(failoverParamsRequestJson: FailoverParamsRequestJson);
  constructor(
    checkpointIdentifier: string,
    commitPolicy: CommitPolicy,
    shutdownPolicy: ShutdownPolicy,
    timeToWaitBeforeShutdownInSec: number,
    commitValue: number
  );
  constructor(
    firstParam: string | FailoverParamsRequest | FailoverParamsRequestJson,
    commitPolicy?: CommitPolicy,
    shutdownPolicy?: ShutdownPolicy,
    timeToWaitBeforeShutdownInSec?: number,
    commitValue?: number
  ) {
    if (typeof firstParam === 'string') {
      // Parameters constructor
      this._json = {
        checkpoint_identifier: firstParam,
        commit_policy: commitPolicy,
        shutdown_policy: shutdownPolicy,
        time_to_wait_before_shutdown_in_sec: timeToWaitBeforeShutdownInSec,
        commit_value: commitValue
      } as FailoverParamsRequestJson;
    } else if (firstParam instanceof FailoverParamsRequest) {
      // Copy constructor
      this._json = firstParam.json;
    } else {
      // Json or empty constructor
      this._json = firstParam || {};
    }
  }

  /**
   * Get checkpoint identifier.
   * @returns {string}
   */
  get checkpointIdentifier(): string {
    return this._json.checkpoint_identifier;
  }

  /**
   * Get commit policy.
   * @returns {VpgFailoverCommitPolicyRequestJson}
   */
  get commitPolicy(): CommitPolicy {
    return this._json.commit_policy;
  }

  /**
   * Get shutdown policy.
   * @returns {VpgFailoverShutdownPolicyRequestJson}
   */
  get shutdownPolicy(): ShutdownPolicy {
    return this._json.shutdown_policy;
  }

  /**
   * Get time to wait before shutdown in sec.
   * @returns {number}
   */
  get timeToWaitBeforeShutdownInSec(): number {
    return this._json.time_to_wait_before_shutdown_in_sec;
  }

  /**
   * Get commit value.
   * @returns {number}
   */
  get commitValue(): number {
    return this._json.commit_value;
  }

  /**
   * Get the json representation of this class.
   * @returns {FailoverParamsRequestJson}
   */
  get json(): FailoverParamsRequestJson {
    return Object.assign({}, this._json);
  }

  /**
   * Get the string representation of this class.
   * @returns {string}
   */
  toString(): string {
    return JSON.stringify(this._json, undefined, 2);
  }
}
