import { EulaSectionJson } from './__json__/eula-section-json';

/**
 * EULA Section
 */
/* istanbul ignore next: autogenerated */
export class EulaSection {
  constructor(private _json: EulaSectionJson) {}

  /**
   * Get info for Eula Section
   * @returns {string}
   */
  get info(): string {
    return this._json.info;
  }

  /**
   * Get license for Eula Section
   * @returns {string}
   */
  get license(): string {
    return this._json.license;
  }

  /**
   * Get the json representation of this class.
   * @returns {EulaSectionJson}
   */
  get json(): EulaSectionJson {
    return Object.assign({}, this._json);
  }

  /**
   * Get the string representation of this class.
   * @returns {string}
   */
  toString(): string {
    return JSON.stringify(this._json, undefined, 2);
  }
}
