import { SupportCategoryJson } from './__json__/support-category-json';

/**
 * Support Category.
 */
/* istanbul ignore next: autogenerated */
export class SupportCategory {
  constructor(private _json: SupportCategoryJson) {}

  /**
   * Get id.
   * @returns {number}
   */
  get id(): number {
    return this._json.id;
  }

  /**
   * Get name.
   * @returns {string}
   */
  get name(): string {
    return this._json.name;
  }

  /**
   * Get type id.
   * @returns {number}
   */
  get typeId(): number {
    return this._json.type_id;
  }

  /**
   * Get service id.
   * @returns {number}
   */
  get serviceId(): number {
    return this._json.service_id;
  }

  /**
   * Get the json representation of this class.
   * @returns {SupportCategoryJson}
   */
  get json(): SupportCategoryJson {
    return Object.assign({}, this._json);
  }

  /**
   * Get the string representation of this class.
   * @returns {string}
   */
  toString(): string {
    return JSON.stringify(this._json, undefined, 2);
  }
}
