export enum BaJobTypeEnum {
  UNKNOWN = 'UNKNOWN',
  BACKUP_VM = 'BACKUPVM',
  REPLICATION_VM = 'REPLICATIONVM',
  COPY_VM = 'COPYVM',
  COPY_FILE = 'COPYFILE',
  FILE_TO_TAPE = 'FILETOTAPE',
  BACKUP_TO_TAPE = 'BACKUPTOTAPE',
  BACKUP_COPY = 'BACKUPCOPY',
  IMMEDIATELY_BACKUP_COPY = 'IMMEDIATELYBACKUPCOPY',
  SQL_LOG_BACKUP = 'SQLLOGBACKUP',
  ORACLE_LOG_BACKUP = 'ORACLELOGBACKUP',
  SURE_BACKUP = 'SUREBACKUP',
  AGENT_POLICY = 'AGENTPOLICY',
  AGENT_BACKUP_JOB = 'AGENTBACKUPJOB',
  BACKUP_FILE = 'BACKUPFILE',
  BACKUP_FILE_COPY = 'BACKUPFILECOPY',
  AZURE_BACKUP_JOB = 'AZUREBACKUPJOB',
  AWS_BACKUP_JOB = 'AWSBACKUPJOB'
}

// pre TS4.1, there is no way to programmatically create a type union from enum *values*
// in TS4.1, can use string literal types to achieve this.
// ex: export type BaJobType = `${BaJobTypeEnum} => will generate the same type union as below

export type BaJobType =
  | 'UNKNOWN'
  | 'BACKUPVM'
  | 'REPLICATIONVM'
  | 'COPYVM'
  | 'COPYFILE'
  | 'FILETOTAPE'
  | 'BACKUPTOTAPE'
  | 'BACKUPCOPY'
  | 'IMMEDIATELYBACKUPCOPY'
  | 'SQLLOGBACKUP'
  | 'ORACLELOGBACKUP'
  | 'SUREBACKUP'
  | 'AGENTPOLICY'
  | 'AGENTBACKUPJOB'
  | 'BACKUPFILE'
  | 'BACKUPFILECOPY'
  | 'AZUREBACKUPJOB'
  | 'AWSBACKUPJOB';
