export enum BaJobStatusEnum {
  UNKNOWN = 'UNKNOWN',
  NONE = 'NONE',
  IDLE = 'IDLE',
  SUCCESS = 'SUCCESS',
  WARNING = 'WARNING',
  FAILED = 'FAILED',
  RUNNING = 'RUNNING',
  STARTING = 'STARTING',
  STOPPING = 'STOPPING',
  ENABLING = 'ENABLING',
  DISABLING = 'DISABLING',
  WAITING_TAPE = 'WAITINGTAPE',
  WAITING_REPOSITORY = 'WAITINGREPOSITORY'
}

// pre TS4.1, there is no way to programmatically create a type union from enum *values*
// in TS4.1, can use string literal types to achieve this.
// ex: export type BaJobStatus = `${BaJobStatusEnum} => will generate the same type union as below

export type BaJobStatus =
  | 'UNKNOWN'
  | 'NONE'
  | 'IDLE'
  | 'SUCCESS'
  | 'WARNING'
  | 'FAILED'
  | 'RUNNING'
  | 'STARTING'
  | 'STOPPING'
  | 'ENABLING'
  | 'DISABLING'
  | 'WAITINGTAPE'
  | 'WAITINGREPOSITORY';
