import { LogInspectionOverTimeSample } from './log-inspection-over-time-sample';
import { LogInspectionOverTimeJson } from './__json__/log-inspection-over-time-json';
import { SerieType } from './__json__/serie-type';

/* istanbul ignore next: autogenerated */
export class LogInspectionOverTime {

  constructor(private _json: LogInspectionOverTimeJson) {
  }

  /**
   * Get org uuid.
   * @returns {string}
   */
  get orgUuid(): string {
    return this._json.org_uuid;
  }

  /**
   * Get type.
   * @returns {SerieType}
   */
  get type(): SerieType {
    return this._json.type;
  }

  /**
   * Get samples.
   * @returns {Array<LogInspectionOverTimeSample>}
   */
  get samples(): Array<LogInspectionOverTimeSample> {
    return this._json.samples.map((sample) => { return new LogInspectionOverTimeSample(sample); });
  }

  /**
   * Get the json representation of this class.
   * @returns {LogInspectionOverTimeJson}
   */
  get json(): LogInspectionOverTimeJson {
    return Object.assign({}, this._json);
  }

  /**
   * Get the string representation of this class.
   * @returns {string}
   */
  toString(): string {
    return JSON.stringify(this._json, undefined, 2);
  }
}
