import { NessusHistoryJson } from './__json__/nessus-history-json';

/**
 * Nessus History.
 */
/* istanbul ignore next: autogenerated */
export class NessusHistory {
  constructor(private _json: NessusHistoryJson) {}

  /**
   * Get history id.
   * @returns {number}
   */
  get historyId(): number {
    return this._json.history_id;
  }

  /**
   * Get uuid.
   * @returns {string}
   */
  get uuid(): string {
    return this._json.uuid;
  }

  /**
   * Get owner id.
   * @returns {number}
   */
  get ownerId(): number {
    return this._json.owner_id;
  }

  /**
   * Get status.
   * @returns {string}
   */
  get status(): string {
    return this._json.status;
  }

  /**
   * Get creation date.
   * @returns {number}
   */
  get creationDate(): number {
    return this._json.creation_date;
  }

  /**
   * Get last modification date.
   * @returns {number}
   */
  get lastModificationDate(): number {
    return this._json.last_modification_date;
  }

  /**
   * Get the json representation of this class.
   * @returns {NessusHistoryJson}
   */
  get json(): NessusHistoryJson {
    return Object.assign({}, this._json);
  }

  /**
   * Get the string representation of this class.
   * @returns {string}
   */
  toString(): string {
    return JSON.stringify(this._json, undefined, 2);
  }
}
