import { ApplicationJson } from './__json__/application-json';
import { ApplicationListJson } from './__json__/application-list-json';

/**
 * Application List
 */
/* istanbul ignore next: autogenerated */
export class ApplicationList {
  constructor(private _json: ApplicationListJson) {}

  /**
   * Get data.
   * @returns {Array<ApplicationJson>}
   */
  get data(): Array<ApplicationJson> {
    return this._json.data;
  }

  /**
   * Get the json representation of this class.
   * @returns {ApplicationListJson}
   */
  get json(): ApplicationListJson {
    return Object.assign({}, this._json);
  }

  /**
   * Get the string representation of this class.
   * @returns {string}
   */
  toString(): string {
    return JSON.stringify(this._json, undefined, 2);
  }
}
