export enum BaJobSchedulingTypeEnum {
  CONTINUOUSLY = 'CONTINUOUSLY',
  PERIODICALLY = 'PERIODICALLY',
  MONTHLY = 'MONTHLY',
  DAILY = 'DAILY',
  CHAINED = 'CHAINED',
  DISABLED = 'DISABLED',
  NOT_SCHEDULED = 'NOTSCHEDULED',
  BACKUP_WINDOW = 'BACKUPWINDOW',
  UNKNOWN = 'UNKNOWN'
}

// pre TS4.1, there is no way to programmatically create a type union from enum *values*
// in TS4.1, can use string literal types to achieve this.
// ex: export type BaJobSchedulingType = `${BaJobTypeSchedulingEnum} => will generate the same type union as below

export type BaJobSchedulingType =
  | 'CONTINUOUSLY'
  | 'PERIODICALLY'
  | 'MONTHLY'
  | 'DAILY'
  | 'CHAINED'
  | 'DISABLED'
  | 'NOTSCHEDULED'
  | 'BACKUPWINDOW'
  | 'UNKNOWN';
