import { O365TeamsRangeExportRequestJson } from './__json__/o365-team-items-export-request-json';

/**
 * O365 Teams export items (posts) given range request
 */
/* istanbul ignore next: autogenerated */
export class O365TeamsRangeExportRequest {
  constructor(private _json: O365TeamsRangeExportRequestJson) {}

  /**
   * Get the start date to export channel posts from.
   * @returns {number}
   */
  get from(): number {
    return this._json.from;
  }

  /**
   * Get the end date to export channel posts until.
   * @returns {number}
   */
  get to(): number {
    return this._json.to;
  }

  /**
   * Get the json representation of this class.
   * @returns {O365TeamsRangeExportRequestJson}
   */
  get json(): O365TeamsRangeExportRequestJson {
    return Object.assign({}, this._json);
  }

  /**
   * Get the string representation of this class.
   * @returns {string}
   */
  toString(): string {
    return JSON.stringify(this._json, undefined, 2);
  }
}
