import { X509CRLJson } from './__json__/x509-crl-json';

/* istanbul ignore next: autogenerated */
export class X509CRL {
  constructor(private _json: X509CRLJson) {}

  /**
   * Get version.
   * @returns {number}
   */
  get version(): number {
    return this._json.version;
  }

  /**
   * Get next update.
   * @returns {number}
   */
  get nextUpdate(): number {
    return this._json.next_update;
  }

  /**
   * Get issuer.
   * @returns {string}
   */
  get issuer(): string {
    return this._json.issuer;
  }

  /**
   * Get the json representation of this class.
   * @returns {X509CRLJson}
   */
  get json(): X509CRLJson {
    return Object.assign({}, this._json);
  }

  /**
   * Get the string representation of this class.
   * @returns {string}
   */
  toString(): string {
    return JSON.stringify(this._json, undefined, 2);
  }
}
