import { ObjectPagingParamsJson } from './__json__/object-paging-params-json';

/* istanbul ignore next: autogenerated */
export class ObjectPagingParams {
  constructor(private _json: ObjectPagingParamsJson) {}

  /**
   * Get page.
   * @returns {number}
   */
  get page(): number {
    return this._json.page;
  }

  /**
   * Get page size.
   * @returns {number}
   */
  get pageSize(): number {
    return this._json.page_size;
  }

  /**
   * Get filter.
   * @returns {string}
   */
  get filter(): string {
    return this._json.filter;
  }

  /**
   * Get the json representation of this class.
   * @returns {ObjectPagingParamsJson}
   */
  get json(): ObjectPagingParamsJson {
    return Object.assign({}, this._json);
  }

  /**
   * Get the string representation of this class.
   * @returns {string}
   */
  toString(): string {
    return JSON.stringify(this._json, undefined, 2);
  }
}
